.bio-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  padding: 20px;
  .profile, .visual {
    margin: 10px;
  }
  .profile {
    padding: 15px;
    width: 55%;
    h4, h1 {
      font-family: 'Comfortaa';
    }
    h1 {
      margin-bottom: 0;
      letter-spacing: 2px;
      font-size: 45px;
    }
    h4 {
      margin: 10px 0 0 0;
      font-size: 17px;
      color: #7a097a;
    }
    .sub-line {
      margin-top: 5px;
      font-size: 15px;
    }
    .profile-btns{
      text-align: center;
      .resume-btn {
        font-family: 'Comfortaa';
        font-weight: bold;
        font-size: 14px;
        color: white;
        text-align: center;
        transition: 0.5s;
        border-radius: 10px;
        letter-spacing: 5px;
        margin: 0 auto;
        padding: 12px;
        border: none;
        -webkit-text-stroke: 0.2px;
        background-size: 200% auto;
        box-shadow: 0 0 0 1px rgba(0,0,0,.04), 0 4px 8px 0 rgba(0,0,0,.2);
        background-image: linear-gradient(to right, #fbc2eb 0%, #a6c1ee 51%, #fbc2eb 100%);
        &:hover {
          color: black;
          cursor: pointer;
          background-position: right center;
        }
      }
    }
  }
  .visual {
    // border: 2px solid red;
    padding: 15px;
    width: 45%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    .image-wrapper {
      border-radius: 50%;
      height: 25vw;
      width: 25vw;
      overflow: hidden;
      box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.04), 0 4px 8px 0 rgba(0, 0, 0, 0.2);
      .me-pic {
        background-size: cover;
        background-position: center;
        height: 25vw;
        box-shadow: inset 0 0 1px #000000;
        border-radius: 50%;
      }
    }
    .glasses {
      margin: 25px;
    }
  }
}
